'use client'

import { useSearchParams } from 'next/navigation'
import { useEffect, useState, Suspense } from 'react'
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import { SkeletonGrid } from '@/components/skeleton-loader'
import { searchAnime, Anime } from '@/lib/api' 
import Link from 'next/link' // Import Link untuk tombol Details
import { Eye, ArrowDownCircle } from 'lucide-react' // Icon
import { Button } from '@/components/ui/button'

function SearchContent() {
  const searchParams = useSearchParams()
  const query = searchParams.get('q') || ''
  const [animes, setAnimes] = useState<Anime[]>([])
  const [loading, setLoading] = useState(true)
  
  // State untuk Load More / See All
  const [page, setPage] = useState(1)
  const [hasMore, setHasMore] = useState(true)
  const [loadingMore, setLoadingMore] = useState(false)

  // Reset search saat query berubah
  useEffect(() => {
    if (!query) {
      setAnimes([])
      setLoading(false)
      return
    }

    const fetchInitial = async () => {
      setLoading(true)
      setPage(1)
      try {
        // Fetch halaman 1
        const res = await searchAnime(query, 1)
        setAnimes(res.data || [])
        // Jika data kurang dari 24, berarti sudah habis (no more pages)
        setHasMore((res.data || []).length === 24) 
      } catch (err) {
        console.error('Search failed:', err)
        setAnimes([])
      } finally {
        setLoading(false)
      }
    }

    const timeoutId = setTimeout(() => {
      fetchInitial()
    }, 500)

    return () => clearTimeout(timeoutId)
  }, [query])

  // Fungsi Load More (See All)
  const handleLoadMore = async () => {
    if (loadingMore || !hasMore) return
    setLoadingMore(true)
    const nextPage = page + 1
    
    try {
      const res = await searchAnime(query, nextPage)
      if (res.data && res.data.length > 0) {
        // Gabungkan data lama + data baru
        setAnimes(prev => [...prev, ...res.data])
        setPage(nextPage)
        // Cek lagi apakah masih ada sisa
        if (res.data.length < 24) setHasMore(false)
      } else {
        setHasMore(false)
      }
    } catch (err) {
      console.error('Load more failed:', err)
    } finally {
      setLoadingMore(false)
    }
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      <div className="mb-8">
        <h1 className="text-4xl font-bold mb-2 text-foreground">
          Search Results
        </h1>
        <p className="text-muted-foreground">
          {query ? `Showing results for "${query}"` : 'Enter a keyword to start searching'}
        </p>
      </div>

      {loading ? (
        <SkeletonGrid /> 
      ) : animes.length > 0 ? (
        <>
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6 animate-in fade-in slide-in-from-bottom-4 duration-500">
            {animes.map((anime, index) => (
              // FIX HYDRATION ERROR: Gunakan DIV sebagai wrapper, bukan Link.
              // AnimeCard sudah memiliki Link internal.
              <div key={`${anime.mal_id}-${index}`} className="group relative block">
                
                {/* Kartu Anime Utama */}
                <AnimeCard anime={anime} />
                
                {/* --- TOMBOL VIEW DETAILS (Overlay) --- */}
                {/* pointer-events-none: Agar klik "tembus" ke Link di dalam AnimeCard */}
                <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-all duration-300 bg-black/60 rounded-xl pointer-events-none z-10">
                  <span className="inline-flex h-10 items-center justify-center rounded-md border border-white/30 bg-white/20 backdrop-blur-md px-6 text-sm font-bold text-white shadow-lg transform translate-y-4 group-hover:translate-y-0 transition-transform duration-300">
                     <Eye size={18} className="mr-2" /> View Details
                  </span>
                </div>

              </div>
            ))}
          </div>

          {/* --- TOMBOL LOAD MORE (SEE ALL) --- */}
          {hasMore && (
            <div className="mt-12 text-center">
              <Button 
                onClick={handleLoadMore} 
                disabled={loadingMore}
                size="lg"
                className="gap-2 bg-secondary text-secondary-foreground hover:bg-secondary/80 min-w-[200px]"
              >
                {loadingMore ? (
                  <span className="animate-spin rounded-full h-4 w-4 border-b-2 border-current"></span>
                ) : (
                  <ArrowDownCircle size={18} />
                )}
                {loadingMore ? 'Loading...' : 'See All Results'}
              </Button>
            </div>
          )}
        </>
      ) : (
        <div className="text-center py-20 bg-secondary/20 rounded-xl border border-dashed border-border">
          <p className="text-muted-foreground mb-2">No results found</p>
          <p className="text-sm text-foreground/60">Try different keywords or check spelling</p>
        </div>
      )}
    </div>
  )
}

export default function SearchPage() {
  return (
    <main className="min-h-screen bg-background">
      <Navbar />
      <Suspense fallback={<div className="pt-20 text-center">Loading search...</div>}>
        <SearchContent />
      </Suspense>
    </main>
  )
}